.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  padding: var(--spacing-large) 0;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.logo {
  &__title {
    color: var(--primary);
    font-weight: 700;
    font-style: italic;
    font-size: var(--size-medium);
  }
}
.nav {
  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: var(--spacing-large);
    }
  }

  &__link {
    position: relative;
    color: var(--primary);
    font-size: var(--size-small);
    transition: color 0.4s;

    @include for-desktop {
      &:hover {
        color: var(--primary);
      }
    }

    &:focus {
      color: var(--primary);
    }
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: -10px;
      transform: translateX(-50%) scaleX(0);
      transform-origin: 50% 50%;
      width: 100%;
      height: 2px;
      background-color: vaR(--primary);
      transition: transform 0.4s;
    }

    @include for-desktop {
      &:hover::after {
        transform: translateX(-50%) scaleX(1);
      }
    }

    &:focus::after {
      transform: translateX(-50%) scaleX(1);
    }
  }
}
