:root {
  --font-family: "Muller";
  --content-width: 1250px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --primary: #153d65;
  --accent-1: #eaf8f5;
  --accent-2: #428783;
  --main-fg-color: #558f3e;
  --secondary-fg-color: #aaaaaa;
  --main-bg-color: #aaaaaa;
  --secondary-bg-color: #aaaaaa;
  --button1-bg-color: rgb(33, 33, 35);
  --button1-fg-color: #2990ea;
  --spacing-small: 10px;
  --spacing-medium: 15px;
  --spacing-large: 20px;
  --spacing-extra-large: 30px;
  --size-small: 16px;
  --size-medium: 18px;
  --size-big: 40px;
  --box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
@font-face {
  font-family: "Muller";
  src: url("../fonts/../fonts/MullerRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  background-color: var(--accent-1);
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  padding: var(--spacing-large) 0;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo__title {
  color: var(--primary);
  font-weight: 700;
  font-style: italic;
  font-size: var(--size-medium);
}

.nav__list {
  display: flex;
}
.nav__item:not(:last-child) {
  margin-right: var(--spacing-large);
}
.nav__link {
  position: relative;
  color: var(--primary);
  font-size: var(--size-small);
  transition: color 0.4s;
}
@media (min-width: 1025px) {
  .nav__link:hover {
    color: var(--primary);
  }
}
.nav__link:focus {
  color: var(--primary);
}
.nav__link::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%) scaleX(0);
  transform-origin: 50% 50%;
  width: 100%;
  height: 2px;
  background-color: vaR(--primary);
  transition: transform 0.4s;
}
@media (min-width: 1025px) {
  .nav__link:hover::after {
    transform: translateX(-50%) scaleX(1);
  }
}
.nav__link:focus::after {
  transform: translateX(-50%) scaleX(1);
}

.hero {
  height: 100vh;
  padding-top: 200px;
  position: relative;
}
.hero::after {
  content: "";
  position: absolute;
  top: 0px;
  bottom: 0;
  left: -250px;
  z-index: -1;
  opacity: 0.1;
  height: 100%;
  width: 50%;
  background: url("../img/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero__text {
  color: var(--primary);
  max-width: 650px;
}
.hero__title {
  margin: 0;
  margin-bottom: var(--spacing-large);
  padding-bottom: var(--spacing-large);
  font-size: var(--size-big);
  position: relative;
}
.hero__title::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 170px;
  height: 3px;
  background-color: var(--primary);
}
.hero__subtitle {
  margin: 0;
  margin-bottom: var(--spacing-extra-large);
  color: var(--accent-2);
  font-weight: normal;
}
.hero__btn {
  padding: var(--spacing-medium) var(--spacing-extra-large);
  border-radius: 10px;
  background-color: var(--accent-2);
  color: var(--light-color);
  font-size: var(--size-medium);
  box-shadow: var(--box-shadow);
  transition: all 0.3s;
}
.hero__btn:hover {
  background-color: rgb(14, 114, 137);
}
.hero__img img {
  border-radius: 100%;
  border: 2px solid var(--primary);
  box-shadow: var(--box-shadow);
}

.scroll-mouse {
  display: block;
  width: 30px;
  height: 50px;
  border: 2px solid var(--primary);
  border-radius: 50px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
}

.scroll-mouse::after,
.scroll-mouse::before {
  content: "";
  position: absolute;
  top: 20%;
  left: 50%;
  height: 10px;
  width: 10px;
  border: 2px solid var(--primary);
  -webkit-transform: translate(-50%, -100%) rotate(45deg);
  -moz-transform: translate(-50%, -100%) rotate(45deg);
  -ms-transform: translate(-50%, -100%) rotate(45deg);
  -o-transform: translate(-50%, -100%) rotate(45deg);
  transform: translate(-50%, -100%) rotate(45deg);
  border-top: transparent;
  border-left: transparent;
  animation: scrollDown 1.5s infinite;
}

.scroll-mouse::after {
  top: 20%;
  animation-delay: 0.4s;
}

@keyframes scrollDown {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    top: 80%;
  }
}